.section-title {
    text-align: center;
    width: 90%;
    margin: 0 auto;
    padding-bottom: 60px;
}

.section-title h2 {
    letter-spacing: 5px;
    margin: 0;
    font-size: 2em;
    text-transform: uppercase;
}
.section-title h2:after {
    content: '';
    display: block;
    width: 10%;
    margin: 10px auto;
    border-bottom: solid #cadbe7;
}
.section-title p {
    width: 50%;
