.header-inner {
    position: relative;
    width: 100%;
    height: 300px;
    padding: 0px;
    margin: 0px;
    z-index: 0;
    overflow: hidden;
}
.header-inner .overlay {
    position: absolute;
    z-index: 1;
}
.header-inner .inner {
    position: absolute;
    width: 100%;
    padding: 0;
    margin: 0px;
    z-index: 3;
}
.header-inner {
    background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url(/RitualBeauty/static/media/header-img.8cf07ac2.jpg) center top no-repeat;
    width: 100%;
    background-size: cover;
    background-color: rgba(0, 0, 0, 0.5);
    opacity: rgba(0, 0, 0, 0.5);
}
